import { Component } from "../core/component"
import { apiService } from "../services/api.service"
import { transformService } from "../services/transform.service"

export class News extends Component {
    constructor( selector, loader ) {
        super( selector )
        this.loader = loader
    }

    init() {
        this.onShaw()
    }

    async onShaw() {
        this.loader?.shaw()

        const fbData = await apiService.getPosts()
        const data = transformService.fbPostsTransform( fbData )
        const HTMLData = data.map( post => postRender( post ) )

        this.loader?.hide()

        this.$el.innerHTML = HTMLData.join("")
    }
}

function postRender( post ) {
    return `
    <div class="news-item">
        <h2 class="news-item__title">
            ${post.title}
        </h2>
        <p class="news-item__text">
            ${post.text}
        </p>
        <div class="news-item__likeNdate">
            <button class="news-item__likeNdate-btn btn">
                <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="#000000" class="heart" viewBox="0 0 16 16">
                    <path d="m8 2.748-.717-.737C5.6.281 2.514.878 1.4 3.053c-.523 1.023-.641 2.5.314 4.385.92 1.815 2.834 3.989 6.286 6.357 3.452-2.368 5.365-4.542 6.286-6.357.955-1.886.838-3.362.314-4.385C13.486.878 10.4.28 8.717 2.01L8 2.748zM8 15C-7.333 4.868 3.279-3.04 7.824 1.143c.06.055.119.112.176.171a3.12 3.12 0 0 1 .176-.17C12.72-3.042 23.333 4.867 8 15z"/>
                </svg>  
            </button>
            <p class="news-item__likeNdate-date">
                ${post.date}
            </p> 
        </div>
    </div>
    `
}